import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import Header from '../components/header';
import Footer from '../components/footer';

import './reset.css';
import './index.css';
import 'bootstrap/dist/css/bootstrap-grid.min.css';
import favicon from './../images/favicon.png';



const Layout = ({ children, data, location }) => {
  const projects = data.allMarkdownRemark.edges;
  const path = location.pathname;
  const match = projects.find(p => p.node.frontmatter.path === path);
  let project;
  if (match) project = match.node.frontmatter;

  return <div>
      {project ? <Helmet>
          <title>{project.title} by Latent Studio</title>
          <meta property="og:title" content={`${project.title} by Latent Studio`} />
          <meta property="og:description" content={project.description} />
          <meta property="og:image" content={data.site.siteMetadata.siteUrl + project.ogImage.publicURL} />
          <meta property="og:image:width" content='1200' />
          <meta property="og:image:height" content='630' />
        </Helmet> : <Helmet>
          <title>{data.site.siteMetadata.title}</title>
          <meta name="description" content="Latent studio is a creative studio specializing in machine learning." />
          <meta name="keywords" content="Building creative projects exploring novel ways of using machine learning." />
          <link rel="icon" type="image/png" sizes="16x16" href={favicon} />
        </Helmet>}

      <Header siteTitle={data.site.siteMetadata.title} />
      <div>{children()}</div>
      <Footer />
    </div>
}

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
        siteUrl
      }
    }
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            description
            ogImage {
              publicURL
            }
            path
            date
          }
        }
      }
    }
  }
`



